import React from 'react'

const BoardLists = (board) => {
  return (
    <>      
        <tr>
          <td>{board.board.bno}</td>
          <td>{board.board.title}</td>
          <td>{board.board.writer}</td>
          <td>{board.board.regdate}</td>
          <td>{board.board.title}</td>
        </tr>
    </>
  )
}

export default BoardLists
